import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, PopoverController, ModalController, ViewController, LoadingController, AlertController, Searchbar, ToastController, ActionSheetController } from 'ionic-angular';
import { SoapService } from '../soap.service';
 
import { Storage } from '@ionic/storage';
import { api_base_url, api_user, api_pass,sc_code } from '../../config';

/**
 * Generated class for the PetugasDirektoratListPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-petugas-direktorat-list',
  providers: [SoapService],
  templateUrl: 'petugas-direktorat-list.html',
})
export class PetugasDirektoratListPage {  
  batasAtas = 1;
  batasBawah = 10;
  petugasDirektoratList:Array<any>=[];
  userdataIPCContact:any;
  isLoading:Boolean = true;
  fakeUsers: Array<any> = new Array(5);

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public modalCtrl: ModalController,
    public popoverCtrl: PopoverController,    
    public viewCtrl: ViewController, 
    public soapService:SoapService,
    public loadingCtrl:LoadingController,
    public storage:Storage,
    public alertCtrl:AlertController,    
    public toastCtrl: ToastController,
    public actionSheetCtrl: ActionSheetController
  ) {
  }

  ionViewDidEnter() {
    this.petugasDirektoratList = [];  
    this.storage.get('userdataIPCContact').then((val) => {
      this.userdataIPCContact = val;           
      this.getPetugasDirektorat('first', '');       
    });   
  }
  
  ionViewDidLoad() {
    console.log('ionViewDidLoad PetugasDirektoratListPage');
  }

  openModal(modalPage) {
    let modal = this.modalCtrl.create(modalPage,{}, {
      enableBackdropDismiss: true,
      showBackdrop:true
      // cssClass:'my-modal'
    });
    modal.present();
    modal.onDidDismiss(data => {  
      // console.log(data);    
      this.petugasDirektoratList = [];
      this.batasAtas = 1;
      this.batasBawah = 10;       
      this.getPetugasDirektorat('first', '');
    });
  }

  getPetugasDirektorat(type, functionName) {    
    this.isLoading = true; 
    this.soapService
    .post(api_base_url,'eoffice_petugas_list_ipcc',{fStream:JSON.stringify(     
      {
        "usernameEDI":api_user,
        "passwordEDI":api_pass,
        "id_dir": this.userdataIPCContact['ID_LEVEL'] == '0' ? "" : this.userdataIPCContact['ID_DIR'],
        "atas":this.batasAtas,
        "bawah":this.batasBawah
      }
    )}).then(result => {
      var responData = JSON.parse(String(result));
            // console.log(responData);
      if (responData['rcmsg'] == "SUCCESS") {    
        if (type == 'refresh' && functionName != '') {
          this.petugasDirektoratList = [];          
        }    
        if (responData['data'].length > 0 && !this.isEmptyObject(responData['data'][0])) {
          for (var i = 0; i < responData['data'].length; i++) {
            this.petugasDirektoratList.push(responData['data'][i]);            
          }
        }
        // console.log(this.petugasDirektoratList);                 
      } else {
        let toast = this.toastCtrl.create({
          message: 'Mohon Maaf Sedang Terjadi Kesalahan, Coba Beberapa Saat Lagi.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present();
      } 
      
      if (type == 'first' && functionName == '' ) {
        
      } else if (type == 'infinite' && functionName != '') {
        functionName.complete();
      } else if (type == 'refresh' && functionName != '') {
        functionName.complete();
      }
      this.isLoading = false;        
    })
    .catch(error => {
      // console.log(error);
      let toast = this.toastCtrl.create({
        message: 'Terjadi Masalah Koneksi, Silahkan Coba Kembali.',
        duration: 3000,
        position: 'bottom'
      });
      toast.present();     
      
      this.isLoading = false;
      if (type == 'first' && functionName == '' ) {        

      } else if (type == 'infinite' && functionName != '') {
        functionName.complete();
      } else if (type == 'refresh' && functionName != '') {
        functionName.complete();
      }      
    });
  }

  doRefresh(refresher) {
    this.batasAtas = 1;
    this.batasBawah = 10;

    this.getPetugasDirektorat('refresh',refresher);
  }

  doInfinite(infiniteScroll) {
    if (this.petugasDirektoratList.length >= 10) {
      this.batasAtas = this.batasBawah + 1;
      this.batasBawah = this.batasBawah + 10;
      this.getPetugasDirektorat('infinite', infiniteScroll);  
    } else {
      infiniteScroll.complete();
    }    
  }

  // openEditPage(val) {
  //   let modal = this.modalCtrl.create('AddPetugasDirektoratPage',{
  //     editData : val
  //   }, {
  //     enableBackdropDismiss: true,
  //     showBackdrop:true
  //     // cssClass:'my-modal'
  //   });
  //   modal.present();

  //   modal.onDidDismiss(data => {   
  //     console.log(data);    
  //     this.petugasDirektoratList = [];
  //     this.batasAtas = 1;
  //     this.batasBawah = 10;       
  //     this.getPetugasDirektorat('first', '');
  //   });
  // }

  showOption(val) {
    let actionSheet = this.actionSheetCtrl.create({
      title: 'Pilih Aksi',
      buttons: [
        {
          text: 'Ubah',
          role: 'koreksiDatang',
          handler: () => {
            // console.log('Ubah clicked');
              let modal = this.modalCtrl.create('AddPetugasDirektoratPage',{
                editData : val
              }, {
                enableBackdropDismiss: true,
                showBackdrop:true
                // cssClass:'my-modal'
              });
              modal.present();

              modal.onDidDismiss(data => {   
                // console.log(data);    
                this.petugasDirektoratList = [];
                this.batasAtas = 1;
                this.batasBawah = 10;       
                this.getPetugasDirektorat('first', '');
              });
          } 
        },
        {
          text: 'Hapus',
          handler: () => {
            // console.log('Hapus clicked');
            this.deleteUser(val);
          }
        },          
        {
          text: 'Tutup',
          role: 'cancel',
          handler: () => {
            // console.log('Cancel clicked');
          }
        }
      ]
    });
 
    actionSheet.present();
  }

  deleteUser(val) {
    let myLoading = this.loadingCtrl.create({
      spinner: 'dots',
      content: "Mohon Tunggu...",       
      cssClass: 'transparent',
      dismissOnPageChange:true
    });
    myLoading.present();    
    this.soapService
    .post(api_base_url,'eoffice_del_user_ipcc',{fStream:JSON.stringify(
      {
        usernameEDI:api_user,
        passwordEDI:api_pass,
        nipp: val['NIPP']      
      }
    )}).then(result => {
      var responData = JSON.parse(String(result));
      // console.log(result);
      if (responData['rcmsg'] == "SUCCESS") {    
        if (responData['data']['HASIL'].includes('Success') ) {

          this.petugasDirektoratList = [];
          this.batasAtas = 1;
          this.batasBawah = 10;       
          let toast = this.toastCtrl.create({
            message: 'Hapus Berhasil.',
            duration: 3000,
            position: 'bottom'
          });
          toast.present().then(() => {
            this.getPetugasDirektorat('first', '');
          });
          
        } else {
          let toast = this.toastCtrl.create({
            message: 'Mohon Maaf Sedang Terjadi Kesalahan, Coba Beberapa Saat Lagi.',
            duration: 3000,
            position: 'bottom'
          });
          toast.present();
        }

      } else {
        let toast = this.toastCtrl.create({
          message: 'Mohon Maaf Sedang Terjadi Kesalahan, Coba Beberapa Saat Lagi.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present();
      }           
      myLoading.dismiss();
    })
    .catch(error => {
      // console.log(error);
      let toast = this.toastCtrl.create({
        message: 'Terjadi Masalah Koneksi, Silahkan Coba Kembali.',
        duration: 3000,
        position: 'bottom'
      });
      toast.present();    
        
      myLoading.dismiss();
    });
  }

  isEmptyObject(obj) {
    for(var prop in obj) {
       if (obj.hasOwnProperty(prop)) {
          return false;
       }
    }  
    return true;
  }

}



